<template lang="html">
  <div class="container w-50 m-auto pt-5">
    <h1 class="text-center display-2">Task List</h1>
    <ul class="list-group">
      <li v-for="item in itemsProperty" class="list-group-item">
        <span>{{item}}</span>
        <button v-on:click="removeItem" class="x">&times;</button>
      </li>
      <li class="list-group-item">
        <input class="form-control" type="text" v-on:keyup.enter="addItem" v-model:value="itemValue" placeholder="Enter Task Here - Hit ENTER to submit">
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
      itemValue: "",
    }
  },
  computed: {
    itemsProperty: function() {
      return this.items;
    }
  },
  methods: {
    addItem: function(event) {
      this.items.push(this.itemValue);
      this.itemValue = "";
    },
    removeItem: function(event) {
      var targetItem = event.target.parentNode.textContent;
      targetItem = targetItem.substring(0, targetItem.length - 1).trim();
      this.items = this.items.filter(item => item !== targetItem)
    },
  }
}
</script>

<style lang="css" scoped>
  .x {
    position: absolute;
    right: 10px;
    top: 45%;
    transform: translateY(-50%);

    background-color: transparent;
    border: none;
    font-size: 30px;
  }

  .x:active {
    outline: none;
  }
</style>



